    addName: function (name) {
        this.used_names.push(name)
    },
    getName: function () {
        let id = -1
        let name = ""
        let tries = 100;

        do {
            id = Math.floor(Math.random() * this.NAMES.length - 1)
            name = this.NAMES[id]
            tries--
        }
        while (this.used_names.includes(name) && tries > 0)

        if(tries == 0){
            return Date.now()
        }
        else {
            this.used_names.push(name)
            return name
        }
    },
